/*
This is the main file where all the different components of OrionCSS are brought together. Remember to append
@import declarations to the correct layer as you create new partials otherwise your code won't be included.
*/
/*
[1] Settings  used with preprocessors and contain font, colors definitions, etc.
[2] Tools  globally used mixins and functions. Its important not to output any CSS in the first 2 layers.
[3] Generic  reset and/or normalize styles, box-sizing definition, etc. This is the first layer which generates actual CSS.
[4] Elements  styling for bare HTML elements. These come with default styling from the browser so we can redefine them here.
[5] Objects  class-based selectors which define undecorated design patterns, for example media object known from OOCSS.
[6] Components  specific UI components. This is where majority of our work takes place and our UI components are often composed of Objects and Components.
[7] Utilities  utilities and helper classes with the ability to override anything which goes before in the triangle. Should primarily be used for positional and minor cosmetic styling (eg bold, no-wrap, text-alignments etc). Every other kind of cosmetic styling should be done in the components layer.
*/
/*
Here we define spacing variants. We don't use variables featuring xs, sm, md, lg etc as to not confuse with
responsive breakpoints. These are used to generate sizing variants of many object and utility classes to give
you a range to choose from. We can create as many as we want without fear of bloating our compiled CSS
file as UNCSS filters out unused css classes.
*/
/*
Set the base font size and line-height for this project.
*/
/*
A comma seperated list of width-set utility class you wish to generate. By default halves, thirds, fourths, fifths and 
twelves are generated, with the last to mimic a 12 column grid system.

For example: .u-1/2, .u-3/12, u-2/5 etc...
*/
/*
Here we can define settings which alter the default state of the grid system - represented by the o-container
and o-layout objects.
*/
/*
This defines default gutter size for o-container and o-layout. Additional sizings can be added via their
spacing modifiers
*/
/*
Here we can define a map of colors. These are filtered out to auto-generate various utility classes, for
example "u-background-mycolor'.
*/
/*
 A mixin to apply clearfix to an element
*/
/*
.myElement {
	@include clearfix();
}
*/
/*
A function to return a specific color from the map in settings.colors
*/
/*
.myElement {
	border-color: color("red");
}
*/
/*
A function to convert any px value into its rem equivalent. 
Sourced from inuitcss (https://github.com/inuitcss/inuitcss)
*/
/*
.myElement {
	padding: rem(16px);
}
*/
/*
A function to return a specific spacing value from the map in settings.spacing
*/
/*
.myElement {
	padding-left: spacing("large");
}
