body,
h1, h2, h3, h4, h5, h6,
blockquote, p, pre,
dl, dd, ol, ul,
figure,
hr,
fieldset, legend {
  margin: 0;
  padding: 0;
  border: 0; }

/*
Remove trailing margins from nested lists.
*/
li > ol,
li > ul {
  margin-bottom: 0; }

/*
Remove default table spacing.
*/
table {
  border-collapse: collapse;
  border-spacing: 0; }

/*
[1] Remove firefox blur on invalid elements
*/
input:invalid,
select:invalid,
textarea:invalid {
  -webkit-box-shadow: none;
          box-shadow: none;
  /* [1] */ }

/*
Resets transparency on input placeholders
*/
::-webkit-input-placeholder {
  opacity: 1; }

::-moz-placeholder {
  opacity: 1; }

:-ms-input-placeholder {
  opacity: 1; }

/*
Remove spinners/arrows for number input
*/
input[type='number'] {
  -moz-appearance: textfield; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

/*
Remove default italics from address element
*/
address {
  font-style: normal; }

/*
Set the global `box-sizing` state to `border-box`.

css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice
paulirish.com/2012/box-sizing-border-box-ftw
*/
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

/*
Keep headers blank and apply styles via component classes avoiding the trap of semantically requiring a
specific header and then having to override styles because cosmetics aren't appropriate.

http://csswizardry.com/2016/02/managing-typography-on-large-apps/
*/
h1, h2, h3, h4, h5, h6 {
  font-size: 1rem; }

/*
[1] Fluid images for responsive purposes.
[2] Offset `alt` text from surrounding copy.
[3] Setting `vertical-align` removes the whitespace that appears under 'img' or 'svg' elements when they are dropped
	into a page as-is.
*/
img {
  max-width: 100%;
  /* [1] */
  font-style: italic;
  /* [2] */
  vertical-align: middle;
  /* [3] */ }

svg {
  vertical-align: middle;
  /* [3] */ }

/*
[1] If a `width` and/or `height` attribute have been explicitly defined, lets not make the image fluid.
*/
img[width],
img[height] {
  /* [1] */
  max-width: none; }

/*
Standard link styles
*/
a {
  text-decoration: none;
  color: inherit; }

/*
Define consistent indentation for lists
*/
dd, ol, ul {
  margin-left: 1em; }

/*
Define some basic spacing for p elements
*/
p {
  margin-bottom: 1.2em; }

p:last-child {
  margin-bottom: 0; }

/*
Set basic styles for HTML top level element.
*/
/*
[1] Set the default `font-size` and `line-height` for the entire project, sourced from our default variables.
     The `font-size` is calculated to exist in ems so the user's browser font-size is respected if changed.
*/
html {
  font-size: 1em;
  /* [1] */
  line-height: 1.5;
  /* [1] */
  overflow-y: scroll;
  min-height: 100%;
  background: -webkit-linear-gradient(315deg, #ffd34d 0%, #ff5640 100%);
  background: -o-linear-gradient(315deg, #ffd34d 0%, #ff5640 100%);
  background: linear-gradient(135deg, #ffd34d 0%, #ff5640 100%);
  background-size: 100%; }

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  max-width: 100%;
  font-family: 'Roboto', sans-serif;
  min-height: 100vh;
  font-size: 20px;
  font-weight: 300;
  color: #333333;
  margin: 30px 0 30px 0; }

